<template>
  <main>
    <form v-on:submit.prevent="registrationHandler">
      <div :class="['one', { active }]">
        <div class="form-inner">
          <label>What's your business name?</label>
          <input class="input" type="text" required v-model="bizName" />

          <label>What's your business registration number?</label>
          <input class="input" type="text" required v-model="regNumber" />

          <label>What's your business cellphone number?</label>
          <input class="input" type="text" required v-model="cell" />

          <label>What's your business address?</label>
          <input class="input" type="text" required v-model="address" />

          <label>What's your email address?</label>
          <input class="input" type="email" required v-model="bizEmail" />

          <label>What's your postal address?</label>
          <input class="input" type="text" required v-model="postalAddr" />

          <button class="next-button" @click="active = true">Next</button>
        </div>
      </div>

      <div :class="['two', { active }]">
        <div class="form-inner">
          <label>What's your business bank account name?</label>
          <input class="input" type="text" required v-model="bankAccName" />

          <label>What's your bank account number?</label>
          <input class="input" type="text" required v-model="bankAccNumber" pattern="[0-9]+" />

          <label>What's your business account branch code?</label>
          <input class="input" type="text" required v-model="branchCode" />

          <label>Abbreviated business name?</label>
          <input class="input" type="text" required v-model="bizAbbr" />

          <label>Account Type:</label>
          <select class="input" v-model="bankAccType">
            <option value="CHECQUE">Cheque Account</option>
            <option value="SAVINGS">Savings Account</option>
            <option value="TRANSMISSION">Transaction Account</option>
          </select>

          <input class="submitbutton" type="submit" value="complete" :disabled="loading" />

          <p v-if="error">An error occurred: {{ registerError }}</p>
        </div>
      </div>
    </form>

    <section v-if="!active">
      <div class="centered">
      <img src="../assets/smartpaySign.png" alt="smartpaysign-logo">
      <h1><p>Welcome To <br>Smartpay</p></h1>
      <h3><p>Lets register your account.</p></h3>
    </div>
    </section>

    <section v-if="active">
      <div class="centered">
      <img src="../assets/smartpaySign.png" alt="smartpaysign-logo">
        <h1>Lets register<br>your account.</h1>
      </div>
    </section>
  </main>
</template>

<script>
import { onLogin } from "../vue-apollo";

export default {
  name: "BusinessRegistration",
  components: {},
  data() {
    return {
      active: false,
      bizName: "",
      regNumber: "",
      bizAbbr: "",
      cell: "",
      address: "",
      bizEmail: "",
      postalAddr: "",
      bankAccName: "",
      bankAccNumber: "",
      bankAccType: "",
      branchCode: "",
      registerError: ""
    };
  },
  methods: {
    registrationHandler() {
      this.$apollo
        .mutate({
          // Query
          mutation: require("../graphql/registerBusiness.gql"),
          // Parameters
          variables: {
            name: this.bizName,
            regNumber: this.regNumber, 
            bizAbbr: this.bizAbbr,
            cell: this.cell,
            address: this.address,
            bizEmail: this.bizEmail,
            postalAddr: this.postalAddr,
            bankAccName: this.bankAccName,
            bankAccNumber: this.bankAccNumber,
            bankAccType: this.bankAccType,
            branchCode: this.branchCode,
          },
        })
        .then((data) => {
          // Result
          onLogin(this.$apolloProvider.defaultClient, data.data.registerBusiness);
          localStorage.setItem("bizname", this.bizName);
          this.$router.push("/launchpad");

          // console.log(data);
        })
        .catch((error) => {
          // Error
          this.registerError = error;
        });
    },
  },
};
</script>

