            <ApolloMutation
              :mutation="require('../graphql/createContract.gql')"
              :variables="{
                clientID,
                paymentMethod,
                noInstallment,
                dateOfirstInstallment,
                installmentAmount: parseFloat(installmentAmount),
                tracking,
                installmentDates,
                collectionReason,
                manualContractID,
              }"
              @done="onDone"
            >
              <template v-slot="{ mutate, loading, error }">
                <form v-on:submit.prevent="mutate()">
                  <div class="form-inner">
                    <div>
