        <div class="modal__footer">
          <slot name="footer" />
          <div class="d-flex align-items-center justify-content-between">
            <button class="btn btn--secondary" @click="closeModal()">
              Cancel
            </button>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: "CreateContractModal",
  props: {
    clientID: String,
  },
  data() {
    return {
      paymentMethod: "",
      noInstallment: "",
      dateOfirstInstallment: "",
      installmentAmount: "",
      tracking: "",
      installmentDates: "",
      collectionReason: "",
      manualContractID: "",
      show: false,
    };
  },
  methods: {
    closeModal() {
      this.show = false;
      document.querySelector("body").classList.remove("overflow-hidden");
    },
    openModal() {
      this.show = true;
      document.querySelector("body").classList.add("overflow-hidden");
    },
    onDone() {
      this.closeModal();
      alert("Contract created!!");
      this.paymentMethod = "";
      this.noInstallment = "";
      this.dateOfirstInstallment = "";
      this.installmentAmount = "";
      this.tracking = "";
      this.installmentDates = "";
      this.collectionReason = "";
      this.manualContractID = "";
    },
  },
};
</script>


