<template>
  <transition name="fade">
    <div class="modal" v-if="show">
      <div class="modal__backdrop" @click="closeModal()" />

      <div class="modal__dialog">
        <div class="modal__header">
          <slot name="header" />
          <button type="button" class="modal__close" @click="closeModal()">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 352 512">
              <path
                fill="currentColor"
                d="M242.72 256l100.07-100.07c12.28-12.28 12.28-32.19 0-44.48l-22.24-22.24c-12.28-12.28-32.19-12.28-44.48 0L176 189.28 75.93 89.21c-12.28-12.28-32.19-12.28-44.48 0L9.21 111.45c-12.28 12.28-12.28 32.19 0 44.48L109.28 256 9.21 356.07c-12.28 12.28-12.28 32.19 0 44.48l22.24 22.24c12.28 12.28 32.2 12.28 44.48 0L176 322.72l100.07 100.07c12.28 12.28 32.2 12.28 44.48 0l22.24-22.24c12.28-12.28 12.28-32.19 0-44.48L242.72 256z"
              ></path>
            </svg>
          </button>

          <div>
            <h2>Deleting Contract</h2>
          </div>
        </div>

        <div class="modal__body">
          <slot name="body" />
          <div>
            <p>Are you sure you want to delete the contract of:</p>
            Client Name = {{ $store.state.clientInfo.name }} <br>
            Contract ID = CN{{ contractID }}
          </div>
        </div>

        <div class="modal__footer">
          <slot name="footer" />
          <div class="d-flex align-items-center justify-content-between">
            <button class="btn btn--secondary" @click="closeModal()">
              Cancel
            </button>
            <button class="btn btn--primary" @click="deleteContract()">
              Delete
            </button>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import gql from 'graphql-tag';
export default {
  name: "DeleteContractModel",
  props: {
    contractID: String,
  },
  data() {
    return {
      show: false,
    };
  },
  methods: {
    closeModal() {
      this.show = false;
      document.querySelector("body").classList.remove("overflow-hidden");
    },
    openModal() {
      this.show = true;
      document.querySelector("body").classList.add("overflow-hidden");
    },
    deleteContract() {
      this.$apollo
        .mutate({
          // Query
          mutation: gql`
            mutation ($contractID: String!) {
              deleteContract(contractID: $contractID)
            }
          `,
          // Parameters
          variables: {
            contractID: this.contractID,
          }
        })
        .then((data) => {
          // Result
          console.log(data);
          this.closeModal();
        })
        .catch((error) => {
          // Error
          console.error(error);
        });
    },
  },
};
</script>


