import Vue from 'vue'
import Vuex from 'vuex'
import 'es6-promise/auto'
import { BootstrapVue, BootstrapVueIcons } from 'bootstrap-vue'
import 'bootstrap-vue/dist/bootstrap-vue-icons.min.css'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import BusinessRegistration from '@/views/BusinessRegistration'
import Launchpad from '@/views/Launchpad'
import LaunchpadHome from '@/views/subLaunchpad/LaunchpadHome'
import CreateClient from '@/views/subLaunchpad/CreateClient'
import EditClientDetails from '@/views/subLaunchpad/EditClientDetails'
import ManageClient from '@/views/subLaunchpad/ManageClient'
import Report from '@/views/subLaunchpad/Report'
import TransactionReports from '@/views/subLaunchpad/TransactionReports'
import CalenderReports from '@/views/subLaunchpad/CalenderReports'
import ClientProfile from '@/views/subLaunchpad/ClientProfile'
import CreateContract from '@/views/subManageClient/CreateContract'
import EditContract from '@/views/subManageClient/EditContract'
import ClientInformation from '@/views/subManageClient/ClientInformation'
import Dashboard from '@/views/subLaunchpad/Dashboard'

Vue.use(VueRouter)
Vue.use(BootstrapVue)
Vue.use(BootstrapVueIcons)
Vue.use(Vuex)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/register',
    name: 'BusinessRegistration',
    component: BusinessRegistration
  },
  {
    path: '/launchpad',
    name: 'Launchpad',
    component: Launchpad,
    children: [
      {
        path: '',
        redirect: 'LaunchpadHome'
      },
      {
        // UserProfile will be rendered inside User's <router-view>
        // when /user/:id/profile is matched
        path: 'launchpadhome',
        name: 'LaunchpadHome',
        component: LaunchpadHome
      },
      {
        // UserProfile will be rendered inside User's <router-view>
        // when /user/:id/profile is matched
        path: 'create',
        name: 'Create',
        component: CreateClient
      },
      {
        // UserProfile will be rendered inside User's <router-view>
        // when /user/:id/profile is matched
        path: 'editClientDetails',
        name: 'EditClientDetails',
        component: EditClientDetails
      },
      {
        // UserPosts will be rendered inside User's <router-view>
        // when /user/:id/posts is matched
        path: 'manage',
        name: 'Manage',
        component: ManageClient
      },
      {
        // UserPosts will be rendered inside User's <router-view>
        // when /user/:id/posts is matched
        path: 'profile/:clientID',
        name: 'ClientProfile',
        component: ClientProfile,
        props: true,
        children: [
          {
            // UserPosts will be rendered inside User's <router-view>
            // when /user/:id/posts is matched
            path: 'contract',
            name: 'Contract',
            component: CreateContract,
            props: true
          },
          {
            // UserPosts will be rendered inside User's <router-view>
            // when /user/:id/posts is matched
            path: 'editContract',
            name: 'EditContract',
            component: EditContract,
            props: true
          },
          {
            // UserPosts will be rendered inside User's <router-view>
            // when /user/:id/posts is matched
            path: 'details',
            name: 'ClientInformation',
            component: ClientInformation,
            props: true
          },
        ]
      },
      {
        // UserPosts will be rendered inside User's <router-view>
        // when /user/:id/posts is matched
        path: 'report',
        name: 'Report',
        component: Report
      },
      {
        // UserPosts will be rendered inside User's <router-view>
        // when /user/:id/posts is matched
        path: 'dashboard',
        name: 'Dashboard',
        component: Dashboard
      },
      {
        // UserPosts will be rendered inside User's <router-view>
        // when /user/:id/posts is matched
        path: 'calenderReports',
        name: 'CalenderReports',
        component: CalenderReports
      },
      {
        // UserProfile will be rendered inside User's <router-view>
        // when /user/:id/profile is matched
        path: 'transactionReports',
        name: 'TransactionReports',
        component: TransactionReports
      }
    ]
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
