<template>
  <main>
    <div :class="['info', { active }]">
      <div id="app" class="box">
        <h1 class="header">{{ $store.state.clientInfo.name }}</h1>
        <section>
          <div class="options">
            <button
              style="
                color: #2e004b;
                border: 1px solid;
                margin-top: 20px;
                border-radius: 30px;
              "
              @click="$refs.viewModal.openModal()"
            >
              View Client Details
            </button>
          </div>

          <template>
            <ApolloQuery
              :query="require('../../graphql/getContract.gql')"
              :variables="{ clientID }"
            >
              <template v-slot="{ result: { loading, error, data } }">
                <!-- Loading -->
                <div v-if="loading" class="loading apollo">Loading...</div>

                <!-- Error -->
                <div v-else-if="error" class="error apollo">
                  An error occurred
                </div>

                <!-- Result -->
                <div v-else-if="data" class="result apollo">
                  <div
                    class="functional-buttons"
                    v-for="contract in data.getContract"
                    :key="contract.contractID"
                    @click="active = false"
                    style="width: 100%, margin-left: -58px"
                  >
                    <router-link
                      class="contract-item"
                      :to="{
                        name: 'ClientInformation',
                        params: {
                          ...contract,
                        },
                      }"
                    >
                      <div class="item-details">
                        <span>CN{{ contract.manualContractID }}</span>
                        <span>{{ contract.paymentMethod }}</span>
                        <span>{{ contract.installmentDates }}</span>
                        <span>{{ contract.installmentAmount }}</span>
                      </div>
                    </router-link>

                    <button
                      class="edit"
                      @click="showEditModal(contract.contractID)"
                      :key="contract.contractID"
                    >
                      Edit
                    </button>

                    <button
                      class="delete"
                      @click="showModal(contract.contractID)"
                      :key="contract.contractID"
                    >
                      Delete
                    </button>
                  </div>
                </div>

                <!-- No result -->
                <div v-else class="no-result apollo">No result :(</div>
              </template>

              <ClientInfoModal ref="viewModal"/>
              <EditContractModal ref="editContractModal"/>
              <DeleteContractModal ref="deleteModal"/>
              <CreateContractModal ref="createContractModal" />
            </ApolloQuery>
          </template>

          <div class="summary">
            <button
              style="color: #2e004b;
                  border: 1px solid;
                  margin-top: 40px;
                  border-radius: 30px;
                "
              @click="showCreateModal(clientID)"
            >
              Add Contract
            </button>
          </div>
        </section>
      </div>
    </div>
    <ClientInfoModal ref="viewModal" />
    <EditContractModal ref="editContractModal" 
      v-bind:contractID="contID"
    />
    <DeleteContractModal ref="deleteModal" 
      v-bind:contractID="contID"
    />
    <CreateContractModal ref="createContractModal" 
      v-bind:clientID="client_ID"
    />
    <div style="flex-grow: 1">
      <router-view></router-view>
    </div>
  </main>
</template>


<script>
import ClientInfoModal from "../../components/ClientInfoModal.vue";
import DeleteContractModal from "../../components/DeleteContractModal.vue";
import EditContractModal from "../../components/EditContractModal.vue";
import CreateContractModal from "../../components/CreateContractModal.vue";

export default {
  components: {
    ClientInfoModal,
    DeleteContractModal,
    EditContractModal,
    CreateContractModal,
  },
  name: "ClientProfile",
  props: {
    clientID: String,
    clientName: String,
    cell: String,
    email: String,
    bankName: String,
    bankAccName: String,
    bankAccNumber: String,
    bankAccType: String,
    biCode: String,
    manualContractID: String
  },
  data() {
    return {
      contID: '',
      client_ID: ''
    };
  },
  mounted: function () {
    if(this.clientName){
      this.$store.commit('addClientInfo', {id:this.clientID, name:this.clientName, cell:this.cell,
                                          email:this.email, bankName:this.bankName, bankAccName:this.bankAccName,
                                          bankAccNumber:this.bankAccNumber, bankAccType:this.bankAccType,
                                          biCode:this.biCode, manualContractID: this.manualContractID})
    }
    // (console.log(this.bankName));
  },
  methods: {
    showModal(contractID){
      this.contID = contractID
      this.$refs.deleteModal.openModal()
    },
    showEditModal(contractID){
      this.contID = contractID
      this.$refs.editContractModal.openModal()
    },
    showCreateModal(clientID){
      this.client_ID = clientID
      this.$refs.createContractModal.openModal()
    }
  }
};
</script>

