                <option value="ENDO">ENDO</option>
                <option value="CARD">Credit Card</option>
              </select>
            </div>

            <div>
              <label>Installment amount</label>
              <input class="input" type="text" required v-model="installmentAmount" />
            </div>

            <div>
              <label>Installment dates</label>
              <select class="select" v-model="installmentDates">
                <option value="MONTHLY">Monthly</option>
                <option value="WEEKLY">Weekly</option>
                <option value="ONCEOFF">Once Off</option>
              </select>
            </div>

            <div>
              <label>Number of installments</label>
              <input class="input" type="text" required v-model="noInstallment" />
            </div>

            <div>
              <label>Date of first installment</label>
              <input class="input" type="date" required v-model="dateOfirstInstallment" />
            </div>

            <div>
              <label>Tracking</label>
              <input class="input" type="text" required v-model="tracking" />
            </div>

            <div>
              <label>Reason for collection</label>
                <select class="select" v-model="collectionReason" required>
                  <option value="21">Insurance Payment</option>
                  <option value="22">Pension Fund Contribution</option>
                  <option value="23">Medical Aid Fund contribution</option>
                  <option value="26">Unit Trust Contribution</option>
                  <option value="28">Charitable or Religious Contribution</option>
                  <option value="31">H.P Payment</option>
                  <option value="32">Account Repayment</option>
                  <option value="33">Loan Repayment (other than mortgage)</option>
                  <option value="34">Rental-Lease (other than property)</option>
                  <option value="35">Service Charge (Maintenane of Service Agreements)</option>
                  <option value="37">Value Added Tax (Vat Allocation)</option>
                  <option value="41">Rent (Property)</option>
                  <option value="42">Bond Repayment</option>
                  <option value="51">Municipal Accounts - Water and Light</option>
                  <option value="52">Municipal Accounts</option>
                  <option value="53">Telephone Accounts</option>
                </select>
            </div>

            <div>
              <label>Contract ID</label>
              <input class="input" type="text" required v-model="manualContractID" />
            </div>

            <div>
              <br/>
              <input class="input" type="submit" value="create" :disabled="loading" style="width: 250px" />
            </div>

            <div class="summary">
              <router-link
                :to="{
                  name: 'ClientProfile',
                  params: { name: 'Contract', params: { clientID } },
                }"
              >
                <button style="color: black">Back</button>
              </router-link>
            </div>

            <p v-if="error">An error occurred: {{ error }}</p>
          </div>
        </form>
      </template>
    </ApolloMutation>
  </main>
</template>


<script>
export default {
  name: "CreateContract",
  props: {
    clientID: String,
  },
  data() {
    return {
      paymentMethod : '',
      noInstallment : '',
      dateOfirstInstallment : '',
      installmentAmount : '',
      tracking : '',
      installmentDates : '',
      collectionReason : '',
      manualContractID : ''
    };
  },
  methods: {
    onDone() {
      alert("Contract created!!");
      this.paymentMethod = ''
      this.noInstallment = ''
      this.dateOfirstInstallment = ''
      this.installmentAmount = ''
      this.tracking = ''
      this.installmentDates = ''
      this.collectionReason = ''
      this.manualContractID = ''
    },
  },
};
</script>

