<template>
  <main>
    <div :class="['form-box', { active }]">
      <!-- <h2>Sign In</h2> -->
      <p v-if="signinError" class="error-msg" >{{ signinError }}</p>
      <form v-on:submit.prevent="signinHandler">
        <!-- <label>Username:</label> -->
        <input class="input"
        style="background-color: transparent;
          color: #eeeeee;
          outline: none;
          outline-style: none;
          border-top: none;
          border-left: none;
          border-right: none;
          border-bottom: solid #eeeeee 1px;
          padding: 3px 10px;"
        type="text" v-model="username" placeholder="Username" required />

        <!-- <label>Password:</label> -->
        <input class="input"
        style="background-color: transparent;
          color: #eeeeee;
          outline: none;
          outline-style: none;
          border-top: none;
          border-left: none;
          border-right: none;
          border-bottom: solid #eeeeee 1px;
          padding: 3px 10px;"
        type="password" v-model="password" placeholder="Password" required />

        <input
          :disabled="loading"
          type="submit"
          value="Login"
          class="input submit-btn"
        />

      </form>
      <div class="click-text" @click="active = true" style="cursor: pointer !important; color: white; margin-left: -58px">I want to Sign Up</div>
    </div>
    <div
      :class="['form-box', { active }]"
      style="background-color: transparent; color: white"
    >
      <!-- <h2>Sign Up</h2> -->
      <p v-if="signupError" class="error-msg">{{ error }}</p>
      <form v-on:submit.prevent="signupHandler">
        <!-- <label>Username:</label> -->
        <input class="input"
        style="background-color: transparent;
          color: #eeeeee;
          outline: none;
          outline-style: none;
          border-top: none;
          border-left: none;
          border-right: none;
          border-bottom: solid #eeeeee 1px;
          padding: 3px 10px;"
        type="text" placeholder="Username" required v-model="rUsername" />

        <!-- <label>Email:</label> -->
        <input class="input"
        style="background-color: transparent;
          color: #eeeeee;
          outline: none;
          outline-style: none;
          border-top: none;
          border-left: none;
          border-right: none;
          border-bottom: solid #eeeeee 1px;
          padding: 3px 10px;"
        type="text" placeholder="Email" required v-model="rEmail" />

        <!-- <label>Password:</label> -->
        <input class="input"
        style="background-color: transparent;
          color: #eeeeee;
          outline: none;
          outline-style: none;
          border-top: none;
          border-left: none;
          border-right: none;
          border-bottom: solid #eeeeee 1px;
          padding: 3px 10px;"
        type="password" placeholder="Password" required v-model="rPassword" />

        <!-- <label>Re-Type Password:</label> -->
        <input class="input"
        style="background-color: transparent;
          color: #eeeeee;
          outline: none;
          outline-style: none;
          border-top: none;
          border-left: none;
          border-right: none;
          border-bottom: solid #eeeeee 1px;
          padding: 3px 10px;"
        type="password" placeholder="Re-Type Password" required v-model="cPassword" />

        <input
          :disabled="loading"
          type="submit"
          value="Sign Up"
          class="input submit-btn"
        />
        
      </form>
      <div class="click-text" @click="active = false" style="cursor: pointer !important; margin-left: -60px">I want to Sign In</div>
    </div>
  </main>
</template>


<script>
import { onLogin } from "../vue-apollo";

export default {
  name: "SignForm",
  props: {
    msg: String,
  },
  data() {
    return {
      active: false,
      username: "",
      password: "",
      rUsername: "",
      rPassword: "",
      cPassword: "",
      rEmail: "",
      signinError: "",
      signupError: "",
      loading: false
    };
  },
  methods: {
    signinHandler() {
      localStorage.clear()
      this.$apollo
        .mutate({
          // Query
          mutation: require("../graphql/signin.gql"),
          // Parameters
          variables: {
            username: this.username,
            password: this.password,
          },
        })
        .then((data) => {
          // Result
          data = data.data.login;

          onLogin(this.$apolloProvider.defaultClient, data.token);
          if (data.businessName) {
            localStorage.setItem("bizname", data.businessName);
            this.$router.push("/launchpad");
          } else {
            this.$router.push("/register");
          }
        })
        .catch((error) => {
          // Error
          this.signinError = "Invalid login";

          setTimeout(()=>{
            console.log('timeed outsafdlh')
            this.signinError = ''
          }, 3000)

          console.log(error);
        });
    },
    signupHandler() {
      localStorage.clear()
      this.$apollo
        .mutate({
          // Query
          mutation: require("../graphql/signup.gql"),
          // Parameters
          variables: {
            username: this.rUsername,
            password: this.rPassword,
            email: this.rEmail,
          },
        })
        .then((data) => {
          // Result
          onLogin(this.$apolloProvider.defaultClient, data.data.register);
          this.$router.push("register");

          // console.log(data);
        })
        .catch((error) => {
          // Error
          this.signupError = "Invalid login";

          setTimeout(()=>{
            this.signupError = ''
          }, 3000)
          console.error(error);
        });
    },
  },
};
</script>

